import { makeStyles } from "@mui/styles";
import { Theme } from "@mui/material/styles";

export const useStyles = makeStyles((theme: Theme) => ({
    root: {
        display: 'flex',
        backgroundColor: 'rgb(26, 26, 26)', //  ,   
        minHeight: '100vh', //    
        color: '#ffffff', //  
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        backgroundColor: '#1e1e1e', // -   
        color: '#ffffff', //  
    },
    drawer: {
        width: 240,
        flexShrink: 0,
    },
    drawerPaper: {
        width: 240,
        backgroundColor: '#1e1e1e', // -   
        color: '#ffffff', //  
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
        backgroundColor: 'rgb(26, 26, 26)', //    
    },
    toolbar: theme.mixins.toolbar,
    footer: {
        padding: theme.spacing(3),
        marginTop: 'auto',
        backgroundColor: '#1e1e1e', // -  
        color: '#ffffff', //  
    },
    form: {
        display: 'flex',
        flexDirection: 'column',
        gap: '16px',
        maxWidth: '400px',
        margin: 'auto',
        padding: '16px',
        border: '1px solid #444', //   
        borderRadius: '8px',
        backgroundColor: '#ffffff', //    
        color: '#000000', //  
        [theme.breakpoints.down('sm')]: {
            width: 'calc(100% - 30px)', //         
            margin: 'auto',
        },
    },
    button: {
        margin: theme.spacing(1),
        backgroundColor: '#1e1e1e', // -  
        color: '#ffffff', //  
        '&:hover': {
            backgroundColor: '#333333', //     
        },
        [theme.breakpoints.down('sm')]: {
            width: '100%', //      
            maxWidth: '200px', //       
            alignSelf: 'center', //     
        }
    },
    filterButtons: {
        display: 'flex',
        justifyContent: 'center',
        gap: '10px',
        margin: '10px 0',
        [theme.breakpoints.down('sm')]: {
            flexDirection: 'row', //      
            flexWrap: 'nowrap', //      
        },
    },
    list: {
        backgroundColor: '#8bb9dd', //    
        color: '#000000', //  
    },
    listItem: {
        borderBottom: '1px solid #ccc', //      
    }
}));
